.latests {
  padding-top: 120px;
  padding-bottom: 65px;
}

.latests__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
}

.latests__head button {
  appearance: none;
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 30px;
  padding-right: 0;
  color: $primary-clr;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }
}

.latests__head button[name="prev"] {
  transform: rotateY(180deg);
}

.latests__info {
  margin-left: $gap--md;
  margin-right: $gap--md;
  text-align: center;
}

.latests__info h2 {
  margin-bottom: 25px;
}

.latests__info a {
  @include underline--center($primary-clr, 100%);
  background-size: 100% 1px;
  color: $second-clr;

  &:active {
    opacity: 0.3;
  }
}

.latests__slider {
  width: 290px;
  margin-bottom: 50px;
}

.latests__pagination {
  margin-left: 15px;
  padding-left: $gap--md;
  padding-right: $gap--md;
  text-align: center;
}

.latests__pagination span {
  margin-right: $gap--sm;
  cursor: pointer;
  color: #9b9997;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }

  &.swiper-pagination-bullet-active {
    color: $second-clr;
    pointer-events: none;
  }
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .latests {
    padding-top: 72px;
    padding-bottom: 30px;
  }

  .latests__head {
    margin-bottom: 50px;
  }

  .latests h2 {
    margin-bottom: 22px;
  }

  .latests__slider {
    margin-bottom: 42px;
  }

  .latests__slide-pict {
    max-width: 324px;
    width: 100%;
  }

  .latests__slide-pict img {
    width: 100%;
  }
}

@media screen and (max-width: $screen--sm) {
  .latests {
    padding-top: 62px;
    padding-bottom: 30px;
  }

  .latests__head {
    justify-content: center;
    margin-bottom: 42px;
  }

  .latests__head button {
    display: none;
  }

  .latests__slider {
    margin-bottom: 35px;
  }

  .latests__slide-pict {
    max-width: 270px;
  }

  .latests__pagination {
    margin-left: 0;
    color: $second-clr;
  }

  .latests__pagination span {
    pointer-events: none;
    margin-right: $gap--sm/2;
    color: $second-clr;
  }
}
