.footer {
  background-color: $black;
  color: $white;
}

.footer__content {
  display: flex;
  flex-wrap: wrap;
  padding-top: 96px;
  padding-bottom: 49px;
}

/* SIGNUP
------------------------------------ */
.footer__signup {
  min-width: 350px;
  max-width: 500px;
  margin: -7px 7.8% 45px 0;
  flex-grow: 1;
}

.footer__signup h2 {
  margin-bottom: 10px;
}

.footer__signup p {
  margin-top: 0;
  margin-bottom: 31px;
  font-weight: 400;
  color: #9b9997;
}

.footer__signup form {
  position: relative;
  align-items: center;
  flex-wrap: nowrap;
}

.footer__signup input {
  @include underline--right($white);
  padding: 10px 150px 10px 0;
  font-size: 14px;
  font-weight: 500;
  color: $white;
  outline: none;
  border-bottom: 1px solid $white;

  &:placeholder {
    color: #9b9997;
  }
}

.footer__signup label {
  color: rgba(#9b9997, 0.7);
}

.footer__signup button {
  position: absolute;
  right: 0;
  flex-grow: 0;
  padding-left: 15px;
  padding-right: 0;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__signup svg {
  margin-left: 12px;
}

/* STORE NAV
------------------------------------ */
.footer__inner-nav {
  display: flex;
  flex-grow: 1;
}

.footer__inner-nav ul {
  margin-right: 13%;
  margin-bottom: 25px;
}

.footer__inner-nav li,
.footer__socials li {
  margin-bottom: 21px;
}

.footer__inner-nav a {
  display: block;
  color: $white;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__socials {
  white-space: nowrap;
  margin-bottom: 25px;
  margin-left: 27px;
}

.footer__socials ul {
  margin-right: 0;
  margin-bottom: 0;
}

.footer__socials svg {
  width: 15px;
  margin-right: 15px;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .footer__content {
    padding-top: 57px;
    padding-bottom: 11px;
    padding-right: 105px;
  }

  .footer__signup {
    min-width: 560px;
    max-width: none;
    margin-right: 60px;
  }

  .footer__inner-nav {
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .footer__inner-nav ul {
    margin-right: $gap--md;
  }

  .footer__socials {
    margin-left: 0;
    margin-right: $gap--md;
  }

  .footer__socials ul {
    margin-right: 0;
  }
}

@media screen and (max-width: $screen--sm) {
  .footer__content {
    padding-right: $gap--sm;
    padding-bottom: 7px;
  }

  .footer__signup {
    min-width: 290px;
    margin-right: 0;
  }

  .footer__signup h2 {
    padding-right: 20%;
  }

  .footer__signup p {
    padding-right: 20%;
    line-height: 22.4px;
  }

  .footer__socials ul {
    display: flex;
    flex-wrap: wrap;
  }

  .footer__socials a {
    display: inline;
  }

  .footer__socials svg {
    width: 20px;
    height: 21px;
    margin-right: 27px;
  }

  .footer__socials span {
    display: none;
  }
}
