.main-intro {
  color: $white;
  text-align: center;
  background-color: #84614d;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.nojs .main-intro,
.no-webp .main-intro {
  background-image: url("../img/intro/intro-main.jpg");

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-main@2x.jpg");
  }
}

.webp .main-intro {
  background-image: url("../img/intro/intro-main.webp");

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-main@2x.webp");
  }
}

.main-intro__content {
  padding-top: 180px;
  padding-bottom: 142px;
}

.main-intro h1 {
  font-size: 45px;
  line-height: 58.5px;
  text-transform: uppercase;
}

.main-intro span {
  @include fz(38px, 49.4px, 400);
  display: block;
  margin-top: -5px;
  letter-spacing: normal;
  text-transform: lowercase;
  font-style: italic;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .nojs .main-intro,
  .no-webp .main-intro {
    background-image: url("../img/intro/intro-main--md.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-main--md@2x.jpg");
    }
  }

  .webp .main-intro {
    background-image: url("../img/intro/intro-main--md.webp");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-main--md@2x.webp");
    }
  }

  .main-intro h1 {
    padding-right: 15px;
  }
}

@media screen and (max-width: $screen--sm) {
  .nojs .main-intro,
  .no-webp .main-intro {
    background-image: url("../img/intro/intro-main--sm.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-main--sm@2x.jpg");
    }
  }

  .webp .main-intro {
    background-image: url("../img/intro/intro-main--sm.webp");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-main--sm@2x.webp");
    }
  }

  .main-intro__content {
    padding-top: 190px;
    padding-bottom: 152px;
  }

  .main-intro h1 {
    padding-right: 0;
    font-size: 38px;
    line-height: 49.4px;
  }

  .main-intro span {
    font-size: 34px;
    line-height: 44.2px;
  }
}
