.goods {
  width: calc(100% - 300px);
}

.goods__list {
  display: grid;
  grid-template: auto / repeat(3, 1fr);
  gap: 38px 30px;
  margin-bottom: 30px;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .goods {
    width: 100%;
  }
}

@media screen and (max-width: $screen--sm) {
  .goods__list {
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
}
