.login-page__main {
  padding-top: 60px;
  padding-bottom: 100px;
}

.login-page__login {
  max-width: $screen--sm;
  margin: 0 auto;
  padding-top: 25%;
}

.login-page__login button[name="close"] {
  display: none;
}

.login-page__login .login__content {
  margin-top: 0;
  padding: 60px $gap--md;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .login-page__login .login__content {
    padding-top: 30px;
  }
}

@media screen and (max-width: $screen--sm) {
  .login-page__login {
    padding-top: 30%;
  }

  .login-page__login .login__content {
    padding-left: $gap--sm;
    padding-right: $gap--sm;
  }
}
