.catalog-intro {
  color: $white;
  text-align: center;
  background-color: #84614d;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  font-family: $font--theme;
}

.nojs .catalog-intro,
.no-webp .catalog-intro {
  background-image: url("../img/intro/intro-catalog.jpg");

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-catalog@2x.jpg");
  }
}

.webp .catalog-intro {
  background-image: url("../img/intro/intro-catalog.webp");

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-catalog@2x.webp");
  }
}

.catalog-intro__content {
  padding-top: 91px;
  padding-bottom: 70px;
}

.catalog-intro h1 {
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: normal;
}

.catalog-intro p {
  @include fz(22px, 28.6px, 500);
  display: block;
  margin-top: -5px;
  letter-spacing: normal;
  font-style: italic;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .nojs .catalog-intro,
  .no-webp .catalog-intro {
    background-image: url("../img/intro/intro-catalog--md.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-catalog--md@2x.jpg");
    }
  }

  .webp .catalog-intro {
    background-image: url("../img/intro/intro-catalog--md.webp");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-catalog--md@2x.webp");
    }
  }
}

@media screen and (max-width: $screen--sm) {
  .nojs .catalog-intro,
  .no-webp .catalog-intro {
    background-image: url("../img/intro/intro-catalog--sm.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-catalog--sm@2x.jpg");
    }
  }

  .webp .catalog-intro {
    background-image: url("../img/intro/intro-catalog--sm.webp");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-catalog--sm@2x.webp");
    }
  }

  .catalog-intro__content {
    padding-top: 77px;
    padding-bottom: 60px;
  }
}
