.modal-overlay {
  z-index: 50;
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba($black, 0.5);
  display: none;

  &.active {
    display: block;
  }
}

.modal {
  z-index: 60;
  overflow: hidden auto;
  display: none;
  position: fixed;
  top: 14%;
  left: 50%;
  max-height: calc(100% - 14%);
  margin-left: -215px;
  width: 430px;
  padding: 55px $gap--md 55px;
  background-color: $grey;

  &.active {
    display: block;
    animation: swing-in-top-fwd 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  }
}

.modal button[name="close"] {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  appearance: none;
  border: none;
  color: inherit;
  font-family: inherit;
  background-color: transparent;
  padding: 22px 20px;
  transition: ease 0.3s;

  &:hover {
    transform: scale(1.5);
  }

  &:active {
    opacity: 0.4;
  }
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .modal {
    width: 442px;
    padding: 55px 51px 50px;
    margin-left: -221px;
    top: 15%;
    max-height: calc(100% - 15%);
  }

  .modal button[name="close"] {
    padding-top: 18px;
    padding-bottom: 18px;
  }

  .modal button[name="close"] svg {
    width: 20px;
    height: 20px;
  }
}

@media screen and (max-width: $screen--sm) {
  .modal {
    width: 270px;
    padding: 40px $gap--sm 40px;
    margin-left: -135px;
    top: 9%;
    max-height: calc(100% - 9%);
  }

  .modal button[name="close"] {
    padding: 15px;
  }

  .modal button[name="close"] svg {
    width: 15px;
    height: 15px;
  }
}
