.header {
  @include fz(14px, 21px, 500);
  color: $second-clr;
}

/* TOP Block
------------------------------------ */
.header__top {
  @include line(1px, #f6f5f4);
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 30px;
}

.header__acts {
  display: flex;
  flex-wrap: wrap;
  width: 30%;
}

.header__acts button {
  display: none;
}

.header__acts-input-group {
  transform: translateY(13px);
  margin-bottom: 14px;
}

.header__acts-input-group input[type="search"] {
  @include underline--fade($primary-clr);
  @include within();
  padding: 6px 6px 6px 30px;
  outline: none;

  &:focus {
    color: $second-clr;
  }

  &:focus ~ svg {
    color: $primary-clr;
  }
}

.header__acts-input-group svg {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.header__logo {
  min-width: 66px;
  margin: 0 17.2%;
  transform: translateY(16px);
}

.header__logo a[href] {
  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.header__user {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  color: inherit;
}

.header__user a {
  color: inherit;
  padding: 16px 20px;
  transition: 0.3s ease;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }
}

.header__user a:last-child {
  padding-right: 0;
}

.header__user svg {
  fill: none;
  display: none;
}

/* NAV Block
------------------------------------ */
.header__nav form {
  display: none;
}

.header__main-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-size: 16px;
  line-height: 24px;
  color: inherit;
}

.header__main-menu a {
  @include underline--both($primary-clr);
  padding: 18px 1.51%;
  font-size: 16px;
  line-height: 24px;
  color: inherit;
  outline: none;

  &:first-child {
    min-width: 128px;
  }
}

.header__second-menu {
  display: none;
}

.header__nav a[href="login.html"] {
  display: none;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  /* TOP Block
  ------------------------------------ */
  .header__top {
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 25px;

    &::after {
      display: none;
    }
  }

  .nojs .header__top {
    margin-bottom: 15px;
  }

  .header__acts {
    width: auto;
  }

  .nojs .header__acts button {
    visibility: hidden;
  }

  .header__acts button {
    display: block;
    flex-grow: 0;
    padding: 14px;
    padding-left: 0;
    transition: 0.3s ease;

    &:hover {
      color: $primary-clr;
      transform: scale(1.2);
    }
  }

  .header__acts form {
    display: none;
  }

  .header__logo {
    transform: none;
    margin: 0 10%;
  }

  .header__user {
    margin-left: 0;
  }

  .header__user a {
    &:hover {
      transform: scale(1.2);
    }
  }

  .header__user a[href="login.html"] {
    display: none;
  }

  .header__user span {
    display: none;
  }

  .header__user svg {
    display: block;
  }

  /* NAV Block
  ------------------------------------ */
  .header__nav {
    display: none;
  }

  .nojs .header__nav {
    display: block;
    padding-bottom: 65px;
  }

  .nojs .header__nav input[type="search"] {
    background-color: rgba($primary-clr, 0.15);
  }

  .header__nav form {
    display: block;
    margin-bottom: 34px;
  }

  .header__input-group input[type="search"] {
    @include underline--fade($primary-clr);
    @include within();
    padding: 8px 8px 8px 40px;
    border-radius: 21px;
    outline: none;
    background-color: rgba($white, 0.15);

    &:focus {
      color: $second-clr;
    }

    &:focus ~ svg {
      color: $primary-clr;
    }
  }

  .header__input-group svg {
    position: absolute;
    top: 50%;
    left: 13px;
    transform: translateY(-50%);
  }

  .header__main-menu {
    flex-direction: column;
    margin-bottom: 15px;
  }

  .header__main-menu a {
    @include underline--right($primary-clr);
    padding: 11px 0;
    font-size: 24px;
    line-height: 36px;

    &:active {
      opacity: 0.3;
    }
  }

  .header__second-menu {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }

  .header__second-menu a {
    @include underline--right($primary-clr);
    padding: 10px 0;
    font-size: 18px;
    line-height: 23.4px;
    opacity: 0.75;
    color: $second-clr;

    &:active {
      opacity: 0.3;
    }
  }

  .header__nav a[href="login.html"] {
    @include underline--right($primary-clr);
    display: block;
    padding: 15px 0;
    font-size: 18px;
    line-height: 23.4px;
    color: $second-clr;

    &:active {
      opacity: 0.3;
    }
  }

  /* NAV with active state */
  .header.active {
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    overflow-y: scroll;
    background-color: $primary-clr;
    color: $white;
  }

  .header.active .header__top {
    margin-bottom: 15px;
  }

  .header.active .header__acts button,
  .header.active .header__user a {
    &:hover {
      color: $white;
    }

    &:active {
      opacity: 0.2;
    }
  }

  .header.active .header__nav {
    display: block;
    padding-bottom: 65px;
  }

  .header.active .header__input-group input[type="search"] {
    color: $white;
    background-image: linear-gradient($white, $white);

    &:focus ~ svg {
      color: $white;
    }
  }

  .header.active .header__input-group label {
    color: rgba($white, 0.7);
  }

  .header.active .header__main-menu a,
  .header.active .header__second-menu a,
  .header.active .header__nav a[href="login.html"] {
    color: $white;
    background-image:
      linear-gradient($white, $white),
      linear-gradient($white, $white);
    background-position:
      0 0,
      0 100%;
  }
}

@media screen and (max-width: $screen--sm) {
  .header__top {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .nojs .header__top,
  .header.active .header__top {
    margin-bottom: 11px;
  }

  .header__logo {
    min-width: 52px;
    width: 52px;
    height: 52px;
  }

  .header__nav form {
    margin-bottom: 30px;
  }

  .header__main-menu,
  .header__second-menu {
    margin-bottom: 11px;
  }

  .header__main-menu a {
    padding: 10px 0;
  }
}
