// COMMON
// =====================================================================
@mixin fz($fz, $lh, $fw) {
  font-size: $fz;
  line-height: $lh;
  font-weight: $fw;
}

@mixin list() {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin centered($max, $gap, $margin: auto) {
  width: 100%;
  max-width: $max;
  margin-left: $margin;
  margin-right: $margin;
  padding-left: $gap;
  padding-right: $gap;
}

@mixin outof() {
  margin-right: calc(-50vw + 50%);
  margin-left: calc(-50vw + 50%);
}

@mixin debug() {
  * {
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.6);
  }
}

@mixin line($h, $c) {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: $h;
    margin-right: calc(-50vw + 50%);
    margin-left: calc(-50vw + 50%);
    background-color: $c;
  }
}


// SHADOWS
// =====================================================================
@mixin shadow() {
  box-shadow:
    0 10px 20px rgba($black, 0.19),
    0 6px 6px rgba($black, 0.23);
}

@mixin shadow--sm() {
  box-shadow: 0 10px 20px rgba($black, 0.19);
}

@mixin shadow--md() {
  box-shaow--md: 0 23px 32px rgba($black, 0.33);
}

// INPUTS
// =====================================================================
@mixin within() {
  &:focus-within {
    @include shadow();
    transform: scale(1.025);
  }
}

@mixin ph--fade() {
  &::placeholder {
    transition: 0.5s ease;
    opacity: 1;
  }

  &:focus::placeholder {
    opacity: 0;
  }
}

@mixin ph--right() {
  &::placeholder {
    transition: 0.6s ease;
    text-indent: 0;
    opacity: 1;
  }

  &:focus::placeholder {
    text-indent: 100vw;
    opacity: 0;
  }
}

// LINKS
// =====================================================================
@mixin underline($clr) {
  transition: ease 0.6s;
  text-decoration: none;
  background-image: linear-gradient($clr, $clr);
  background-repeat: no-repeat;
  background-size: 0 2px;
  background-position: 50% 100%;

  &:hover,
  &:focus {
    color: $clr;
    background-size: 100% 2px;
  }
}

@mixin underline--fade($clr) {
  @include underline($clr);
  background-size: 100% 0;
}

@mixin underline--right($clr) {
  @include underline($clr);
  background-position: 0 100%;
}

@mixin underline--center($clr, $size: 50%) {
  @include underline($clr);
  background-position: 50% 100%;
  background-size: $size 2px;
}

@mixin underline--left($clr) {
  @include underline($clr);
  background-size: 0 2px;
  background-position: 100% 100%;
}

@mixin underline--both($clr) {
  @include underline($clr);
  background-image:
    linear-gradient($clr, $clr),
    linear-gradient($clr, $clr);
  background-position: 50% 0, 50% 100%;
}


// ANIMATIONS
// =====================================================================
// SWING
// ------------------------------------
// animation: swingY 1s ease-in-out infinite
@mixin swingY() {
  @keyframes swingY {
    0%,
    100% {
      transform: translateY(-8px);
    }

    50% {
      transform: translateY(8px);
    }
  }
}

@mixin swingX() {
  @keyframes swingX {
    0%,
    100% {
      transform: translateX(8px);
    }

    50% {
      transform: translateX(8px);
    }
  }
}

// SHAKE
// ------------------------------------
// animation: shake 0.6s ease;
@mixin shake() {
  @keyframes shake {
    0%,
    100% {
      transform: translateX(0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translateX(-10px);
    }

    20%,
    40%,
    60%,
    80% {
      transform: translateX(10px);
    }
  }
}

// BOUNCE
// ------------------------------------
// animation: bounce-in-top 1.2s both;
@mixin bounce-in-top() {
  @keyframes bounce-in-top {
    0% {
      transform: translateY(-500px);
      animation-timing-function: ease-in;
      opacity: 0;
    }

    38% {
      transform: translateY(0);
      animation-timing-function: ease-out;
      opacity: 1;
    }

    55% {
      transform: translateY(-65px);
      animation-timing-function: ease-in;
    }

    72% {
      transform: translateY(0);
      animation-timing-function: ease-out;
    }

    81% {
      transform: translateY(-28px);
      animation-timing-function: ease-in;
    }

    90% {
      transform: translateY(0);
      animation-timing-function: ease-out;
    }

    95% {
      transform: translateY(-8px);
      animation-timing-function: ease-in;
    }

    100% {
      transform: translateY(0);
      animation-timing-function: ease-out;
    }
  }
}

// Comment SWING
// ------------------------------------
// animation: swing-in-top-fwd 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
@mixin swing-in-top-fwd() {
  @keyframes swing-in-top-fwd {
    0% {
      transform: rotateX(-100deg);
      transform-origin: top;
      opacity: 0;
    }

    100% {
      transform: rotateX(0deg);
      transform-origin: top;
      opacity: 1;
    }
  }
}

// Comment SLIDE
// ------------------------------------
// animation: slide-in-fwd-center 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
@mixin slide-in-fwd-center() {
  @keyframes slide-in-fwd-center {
    0% {
      transform: translateZ(-1400px);
      opacity: 0;
    }

    100% {
      transform: translateZ(0);
      opacity: 1;
    }
  }
}
