.login {
  @include fz(14px, 21px, 500);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: contain;
  box-shadow: 0 5px 32px rgba(226, 221, 215, 0.24);
}

.login__content {
  margin-top: 135px;
  background-color: $grey;
}

.nojs .login,
.no-webp .login {
  background-image: url("../img/intro/intro-login.jpg");

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-login@2x.jpg");
  }
}

.webp .login {
  background-image: url("../img/intro/intro-login.webp");

  @media only screen and (min-resolution: 2dppx) {
    background-image: url("../img/intro/intro-login@2x.webp");
  }
}

.login h2 {
  text-align: center;
  margin-bottom: 24px;
}

.login__input-group {
  width: 100%;
  margin-bottom: 25px;
}

.login input {
  @include underline--fade($primary-clr);
  font-weight: 500;
  box-shadow: 0 1px 0 0 $second-clr;
  padding-top: 9px;
  padding-bottom: 9px;
  outline: none;

  &:focus {
    color: $second-clr;
  }
}

.login label {
  font-size: 12px;
}

.login__input-group--password {
  margin-bottom: 45px;
}

.login__input-group--password a {
  display: block;
  margin-top: 8px;
  text-align: right;
  color: #9b9997;
  font-size: 12px;
  line-height: 18px;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }
}

.login button[type="submit"] {
  width: 100%;
  padding: 22px;
  margin-bottom: 17px;
  background: $primary-clr;
  color: $white;
  transition: ease 0.3s;

  &:hover {
    box-shadow: inset 0 0 0 6px $primary-clr;
    background-color: $white;
    color: $second-clr;
  }

  &:active {
    box-shadow: inset 0 0 0 100px $primary-clr;
    opacity: 1;
  }
}

.login__signup {
  color: #9b9997;
  text-align: center;
  flex-grow: 1;
}

.login__signup a {
  margin-left: 5px;
  color: $second-clr;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .nojs .login,
  .no-webp .login {
    background-image: url("../img/intro/intro-login--md.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-login--md@2x.jpg");
    }
  }

  .webp .login {
    background-image: url("../img/intro/intro-login--md.webp");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-login--md@2x.webp");
    }
  }

  .login__content {
    margin-top: 130px;
  }
}

@media screen and (max-width: $screen--sm) {
  .nojs .login,
  .no-webp .login {
    background-image: url("../img/intro/intro-login--sm.jpg");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-login--sm@2x.jpg");
    }
  }

  .webp .login {
    background-image: url("../img/intro/intro-login--sm.webp");

    @media only screen and (min-resolution: 2dppx) {
      background-image: url("../img/intro/intro-login--sm@2x.webp");
    }
  }

  .login__content {
    margin-top: 80px;
  }

  .login h2 {
    margin-bottom: 18px;
    font-size: 24px;
    line-height: 31.2px;
  }

  .login__input-group {
    margin-bottom: 20px;
  }

  .login__input-group--password {
    margin-bottom: 32px;
  }

  .login button[type="submit"] {
    padding: 23px;
  }
}
