.card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  &:hover img {
    @include shadow();
    transition: all 0.3s;
  }
}

.card__pict {
  margin-bottom: 15px;
}

.card a {
  text-align: center;
  padding-left: $gap--sm;
  padding-right: $gap--sm;
  color: $second-clr;

  &:hover {
    color: $primary-clr;
  }

  &:hover span {
    color: $second-clr;
  }

  &:active {
    opacity: 0.3;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.card span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5px;
  font-weight: 700;
  font-size: 14px;
  line-height: 18.2px;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .card a {
    padding: 0;
  }
}

@media screen and (max-width: $screen--sm) {
  .card__pict {
    margin-bottom: 8px;
  }
}
