.catalog-content {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding-top: 80px;
  padding-bottom: 37px;
}

.catalog-content button[name="filter"] {
  display: none;
}

.catalog-content__pagination {
  @include line(1px, #f6f5f4);
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 20px 0 16px 300px;

  &::after {
    top: 0;
  }
}

.catalog-content__pagination a {
  padding: 22px 6.5px;
  color: #9b9997;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }

  &.active {
    pointer-events: none;
    color: $second-clr;
  }
}

.catalog-content__pagination > a {
  color: $second-clr;
}

.catalog-content__pagination > a:first-child {
  margin-right: $gap--md;
  padding-left: 0;
}

.catalog-content__pagination > a:last-child {
  margin-left: $gap--md;
  padding-right: 0;
}

.catalog-content__pagination ul {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  transform: translateX(-16px);
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .catalog-content {
    padding-top: 50px;
    padding-bottom: 35px;
  }

  .catalog-content button[name="filter"] {
    @include fz(16px, 20.8px, 500);
    display: block;
    margin-bottom: 50px;
  }

  .catalog-content__pagination {
    padding-left: 0;
  }
}

@media screen and (max-width: $screen--sm) {
  .catalog-content {
    padding-top: 40px;
    padding-bottom: 51px;
  }

  .catalog-content button[name="filter"] {
    margin-bottom: 38px;
  }

  .catalog-content__pagination {
    justify-content: center;
    padding: 13px $gap--sm;
  }

  .catalog-content__pagination ul {
    transform: unset;
  }

  .catalog-content__pagination a {
    padding-left: 11px;
    padding-right: 11px;
  }

  .catalog-content__pagination > a {
    display: none;
  }
}
