.examples {
  padding-top: 42px;
  padding-bottom: 130px;
}

.examples__head {
  width: 100%;
  text-align: center;
  margin-bottom: 65px;
  padding-left: $gap--md;
  padding-right: $gap--md;
}

.examples__head h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 31px;
}

.examples__head svg {
  min-width: 27px;
}

.examples__head a {
  @include underline--center($primary-clr, 100%);
  background-size: 100% 1px;
  color: $second-clr;

  &:active {
    opacity: 0.3;
  }
}

.examples__gallery {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-template-rows: auto;
  gap: 34px 27px;
}

.examples__gallery-photo img {
  object-fit: cover;
  display: block;
  width: 100%;
  height: 100%;
}

.examples__gallery-photo--2 {
  grid-column: 2 / 3;
  grid-row: 1 / 3;
  max-width: 399px;
  min-width: 231px;
}

/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .examples {
    padding-top: 45px;
    padding-bottom: 79px;
  }

  .examples__head {
    margin-bottom: 63px;
  }

  .examples h2 {
    margin-bottom: 19px;
  }

  .examples__gallery {
    gap: 20px 16px;
  }
}

@media screen and (max-width: $screen--sm) {
  .examples {
    padding-top: 32px;
    padding-bottom: 72px;
  }

  .examples__head {
    margin-bottom: 47px;
    padding-left: $gap--sm;
    padding-right: $gap--sm;
  }

  .examples__gallery {
    grid-template-columns: 1fr 1fr;
    gap: 27px 24px;
  }

  .examples__gallery-photo--2 {
    min-width: initial;
  }

  .examples__gallery-photo--3 {
    display: none;
  }

  .examples__gallery-photo--4 {
    display: none;
  }
}
