html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  @include fz(16px, 20.8px, 500);
  overflow-x: hidden;
  display: grid;
  grid-template-rows: auto 1fr auto;
  position: relative;
  min-width: $min-width;
  min-height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: $font;
  font-style: normal;
  color: $second-clr;
  background-color: $white;

  &.noscroll {
    overflow-y: hidden;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.centered {
  @include centered($screen--xl, $gap--lg);
}

.v-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
}

[hidden] {
  display: none;
}

[disabled],
.disabled {
  pointer-events: none;
  opacity: 0.3;
}

.list {
  & ol,
  & ul {
    @include list();
  }
}

.title h1,
.title h2,
.title h3,
.title h4,
.title h5,
.title h6 {
  @include fz(34px, 44.2px, 700);
  font-family: $font--theme;
  margin-top: 0;
  letter-spacing: 0.03em;
}

a {
  text-decoration: none;
}

@include swingY();
@include swing-in-top-fwd();


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .centered {
    padding-left: $gap--md;
    padding-right: $gap--md;
  }
}

@media screen and (max-width: $screen--sm) {
  .centered {
    padding-left: $gap--sm;
    padding-right: $gap--sm;
  }
}
