.filter {
  min-width: 250px;
  width: 250px;
  margin-right: 50px;
  padding-bottom: 15px;
  background-color: $white;
}

.filter button[name="close"] {
  display: none;
}

.filter form {
  padding-top: 8px;
}

.filter__item {
  width: 100%;
  box-shadow: 0 0 1px 0 $primary-clr;
  transition: 0.3s ease;

  &:hover {
    box-shadow: 0 0 5px 0 $primary-clr;
  }

  &.active {
    padding-bottom: 15px;
  }

  &.active .filter__item-title {
    padding-bottom: 14px;
  }

  &.active .filter__item-content {
    display: block;
  }

  &.active .filter__item-title svg:first-child {
    display: block;
  }

  &.active .filter__item-title svg:last-child {
    display: none;
  }
}

.filter__item-title {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 23px 10px 25px;
  word-break: break-all;

  &:hover {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }
}

.filter__item-title button {
  transition: ease 0.3s;
  padding-left: 0;
  padding-right: 0;
  margin-left: $gap--sm;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &:hover svg {
    animation: swingY 1s ease-in-out infinite;
  }
}

.filter__item-title svg:first-child {
  display: none;
}

.filter__item-content {
  display: none;
  padding-left: 25px;
  padding-right: 30px;
}

.filter__item-content input[type="checkbox"] {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);

  &:checked + label::before {
    background-color: $primary-clr;
  }

  &:focus + label::before {
    outline: #2d3ec6 solid 1px;
    box-shadow: 0 0 6px #2d3ec6;
  }
}

.filter .form__input-group {
  margin-bottom: 12px;
}

.filter label {
  display: flex;
  padding-left: 11px;
  font-size: 14px;
  line-height: 18.2px;
  white-space: nowrap;

  &:active {
    opacity: 0.3;
  }

  &::before {
    content: "";
    width: 15px;
    height: 15px;
    margin-right: 10px;
    box-shadow: inset 0 0 0 1px $primary-clr;
  }

  &:hover::before {
    box-shadow: 0 0 3px 2px $black--5;
  }
}

.filter__range {
  position: relative;
  margin-bottom: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.filter__range-line,
.filter__range-line-active {
  height: 1px;
  width: 100%;
  background-color: $grey;
}

.filter__range-line-active {
  width: 105px;
  transform: translateX(55px);
  background-color: $primary-clr;
}

.filter__range label {
  z-index: 3;
  position: absolute;
  top: 50%;
  transform: translateY(-8px);
  display: block;
  padding-left: 0;

  &::before {
    display: none;
  }

  &:active {
    opacity: 1;
  }
}

.filter__range label:first-of-type {
  left: 38px;
}

.filter__range label:last-of-type {
  left: 143px;
}

.filter__range input {
  display: block;
  width: 15px;
  height: 15px;
  margin-bottom: 8px;
  margin-left: 7px;
  border-radius: 50%;
  background-color: $primary-clr;
  cursor: pointer;
  pointer-events: none;
  font-size: 0;
  transition: all 0.3s ease;

  &:hover {
    transform: scale(1.3);
  }
}

.filter__form-controls {
  width: 100%;
  margin-top: 35px;
}

.filter__form-controls button {
  margin-bottom: 24px;
}

.filter__form-controls button[type="reset"] {
  box-shadow: inset 0 0 0 1px $primary-clr;
  background-color: $white;
  color: $second-clr;

  &:hover {
    box-shadow: inset 0 0 0 6px $primary-clr;
  }

  &:active {
    box-shadow: inset 0 0 0 100px $primary-clr;
  }
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .nojs .filter button[name="filter"] {
    display: none;
  }

  .nojs .filter {
    position: static;
    width: 70%;
    margin: 0 auto 50px;
  }

  .nojs .filter button[name="close"] {
    display: none;
  }

  .nojs .filter__content {
    max-height: none;
    overflow-y: visible;
  }

  .nojs .filter__item:hover {
    box-shadow: 0 1px 0 0 $primary-clr;
  }

  .nojs .filter__item-title {
    &:hover {
      color: $second-clr;
    }

    &:active {
      opacity: 1;
    }
  }

  .nojs .filter__item button[name="toggle"] {
    display: none;
  }

  .nojs .filter__item-content {
    display: block;
  }

  .filter {
    z-index: 60;
    position: fixed;
    top: 27px;
    left: -100%;
    width: 423px;
    padding-top: 9px;
    margin-right: 0;
    box-shadow: 0 5px 32px rgba(219, 213, 208, 0.24);
    transition: all 0.6s ease;

    &.active {
      left: 0;
    }
  }

  .filter__content {
    max-height: calc(100vh - 27px);
    overflow-y: scroll;
  }

  .filter button[name="close"] {
    position: absolute;
    top: -30px;
    right: -30px;
    display: block;
    color: $black;
    padding: 14px;
    border: none;
    cursor: pointer;
    appearance: none;
    background: transparent;
    transition: all 0.3s ease;

    &:hover {
      color: $primary-clr;
      transform: scale(1.2);
    }

    &:active {
      opacity: 0.7;
    }
  }

  .filter form {
    padding-top: 0;
  }

  .filter__item {
    box-shadow: 0 1px 0 0 $primary-clr;

    &.active {
      padding-bottom: 17px;
    }
  }

  .filter__item-title {
    padding: 20px 40px 24px 21px;
    font-size: 18px;
    line-height: 23.4px;
  }

  .filter__item-title button {
    flex-grow: initial;
  }

  .filter__item-title svg {
    width: 23px;
    height: 12px;
  }

  .filter .form__input-group {
    margin-bottom: 18px;
  }

  .filter label {
    padding-left: 6px;
    font-size: 16px;
    line-height: 20.8px;

    &::before {
      width: 18px;
      height: 18px;
    }
  }

  .filter__range {
    margin-bottom: 37px;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-right: 88px;
  }

  .filter__range-line-active {
    width: 189px;
  }

  .filter__range input {
    width: 18px;
    height: 18px;
  }

  .filter__range label:last-of-type {
    left: 215px;
  }

  .filter__form-controls {
    margin-top: 40px;
    padding-left: 21px;
    padding-right: 40px;
  }
}

@media screen and (max-width: $screen--sm) {
  .nojs .filter {
    width: 80%;
  }

  .filter {
    width: 270px;
    top: 75px;
    padding-top: 8px;
    padding-bottom: 0;

    &.active {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .filter button[name="close"] {
    top: -29px;
    right: -24px;
  }

  .filter__content {
    max-height: calc(100vh - 75px);
  }

  .filter__item {
    &.active {
      padding-bottom: 7px;
    }

    &--price .filter__item-title {
      padding-top: 17px;
      padding-bottom: 17px;
    }

    &--price.active .filter__item-title {
      padding-bottom: 10px;
    }
  }

  .filter__item-title {
    padding-right: 18px;
    padding-top: 11px;
    padding-bottom: 11px;
  }

  .filter__item--price .filter__item-content {
    padding-left: 10px;
    padding-right: 20px;
  }

  .filter__range-line-active {
    width: 135px;
  }

  .filter__item--price .filter__range {
    padding-right: 0;
  }

  .filter__range label:first-of-type {
    left: 26px;
  }

  .filter__range label:last-of-type {
    left: 163px;
  }

  .filter__form-controls {
    margin-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
