.faq {
  max-width: 1170px;
  padding-top: 53px;
  padding-bottom: 65px;
}

.faq__head {
  margin-bottom: 49px;
  padding-left: 38px;
  padding-right: 38px;
  text-align: center;
}

.faq__head h2 {
  margin-bottom: 20px;
}

.faq__item {
  box-shadow: 0 0 1px 0 $primary-clr;
  transition: 0.3s ease;

  &:hover {
    box-shadow: 0 0 5px 0 $primary-clr;
  }

  &.active {
    padding-bottom: 25px;
  }

  &.active svg:first-child {
    display: block;
  }

  &.active svg:last-child {
    display: none;
  }

  &.active .faq__content {
    display: block;
  }

  &.active .faq__title {
    padding-bottom: 8px;
  }
}

.nojs .faq__item {
  padding-bottom: 25px;
  pointer-events: none;

  & button {
    display: none;
  }
}

.nojs .faq__content {
  display: block;
}

.nojs .faq__title {
  padding-bottom: 8px;
}

.faq__title {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 44px 48px;

  &:hover p {
    color: $primary-clr;
  }

  &:active {
    opacity: 0.3;
  }
}

.faq__title p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 23.4px;
  transition: 0.3s ease;
}

.faq__title br {
  display: none;
}

.faq__title button {
  cursor: pointer;
  appearance: none;
  border: none;
  font-family: inherit;
  background-color: transparent;
  transition: ease 0.3s;
  color: $primary-clr;
  margin-left: $gap--md;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  &:hover svg {
    animation: swingY 1s ease-in-out infinite;
  }
}

.faq__title svg:first-child {
  display: none;
}

.faq__content {
  display: none;
  max-width: 620px;
  padding-left: 48px;
  padding-right: 48px;
}

.faq__content p {
  margin-top: 0;
  font-weight: 400;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .faq {
    padding-top: 40px;
    padding-bottom: 30px;
  }

  .faq__head {
    margin-bottom: 50px;
  }

  .faq__head h2 {
    margin-bottom: 22px;
  }

  .faq__item {
    &.active .faq__title {
      padding-bottom: 25px;
    }
  }

  .nojs .faq__title {
    padding-bottom: 25px;
  }

  .faq__title {
    padding-right: 44px;
  }

  .faq__title svg {
    width: 23px;
    height: 12px;
  }

  .faq__content {
    max-width: 630px;
  }
}

@media screen and (max-width: $screen--sm) {
  .faq {
    padding-top: 29px;
  }

  .faq__head {
    margin-bottom: 28px;
    padding-left: $gap--sm;
    padding-right: $gap--sm;
  }

  .faq__item {
    &.active {
      padding-bottom: 10px;
    }

    &.active .faq__title {
      padding-bottom: 14px;
    }
  }

  .nojs .faq__item {
    padding-bottom: 10px;
  }

  .nojs .faq__title {
    padding-bottom: 14px;
  }

  .faq__title {
    align-items: flex-start;
    padding: 16px 20px 20px 23px;
  }

  .faq__title p {
    font-size: 16px;
    line-height: 22.4px;
  }

  .faq__title br {
    display: initial;
  }

  .faq__title button {
    margin-left: 5px;
    margin-top: 7px;
  }

  .faq__title svg {
    width: 20px;
    height: 10px;
  }

  .faq__content {
    padding-left: 23px;
    padding-right: 60px;
  }

  .faq__content p {
    font-size: 14px;
    line-height: 19.6px;
  }
}
