.btn > button,
.btn > input[type="button"],
.btn > a {
  cursor: pointer;
  background: $primary-clr;
  font-family: $font;
  appearance: none;
  border: none;
  color: $white;
  padding: 24px;
  transition: ease 0.3s;
  width: 100%;

  &:hover {
    box-shadow: inset 0 0 0 6px $primary-clr;
    background-color: $white;
    color: $second-clr;
  }

  &:active {
    opacity: 1;
    box-shadow: inset 0 0 0 100px $primary-clr;
    color: $white;
  }
}
