.form {
  position: relative;
}

.form form {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.form__input-group {
  flex-grow: 1;
  position: relative;
}

.form input,
.form textarea {
  width: 100%;
  appearance: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  background-color: transparent;

  &::placeholder {
    color: #9b9997;
  }

  &:focus + label {
    opacity: 1;
  }

  &.error {
    box-shadow: 0 0 6px 6px $error-clr;
  }

  @include ph--fade();
}

.form label {
  position: absolute;
  top: -18px;
  left: 6px;
  opacity: 0;
  font-size: 14px;
  line-height: 16px;
  color: rgba($primary-clr, 0.7);
  transition: ease 0.3s;
  cursor: pointer;
  pointer-events: none;
}

.form__input-group-static label {
  position: static;
  pointer-events: auto;
  opacity: 1;
  color: $second-clr;
}

.form__input-group-static input {
  background-color: initial;
  appearance: auto;
  width: auto;
}

.form button {
  @include fz(16px, 20.8px, 500);
  padding: 14px $gap--md;
  cursor: pointer;
  appearance: none;
  border: none;
  color: inherit;
  font-family: inherit;
  background-color: transparent;

  &:active {
    opacity: 0.3;
  }
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .form {
    max-width: 100%;
  }

  .form__input-group {
    flex-grow: 1;
  }

  .form button {
    flex-grow: 1;
  }
}
