.blog {
  padding-top: 53px;
  padding-bottom: 65px;
}

.blog__head {
  text-align: center;
  margin-bottom: 65px;
  padding-left: $gap--md;
  padding-right: $gap--md;
}

.blog__head h2 {
  margin-bottom: 20px;
}

.blog__head a {
  @include underline--center($primary-clr, 100%);
  background-size: 100% 1px;
  color: $second-clr;

  &:active {
    opacity: 0.3;
  }
}

.blog__previews {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  gap: 30px;
}

.blog__preview {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;

  &--lg {
    grid-template-rows: minmax(300px, 1fr) auto;
    grid-column: 2 / 3;
    grid-row: 1 / 3;
  }

  &--lg .blog__preview-info {
    z-index: 1;
    grid-column: 2 / 3;
    grid-row: 2 / 3;
  }

  &--lg .blog__preview-img {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
  }

  &--invert .blog__preview-img {
    order: -1;
  }
}

.blog__preview-info {
  min-height: 300px;
  padding: 65px $gap--sm;
  text-align: center;
  background-color: $grey;
}

.blog__preview-info time {
  display: block;
  margin-bottom: 12px;
  font-size: 14px;
  line-height: 19.2px;
  color: #9b9997;
}

.blog__preview-info h3 {
  @include fz(26px, 33.8px, 600);
  color: $black;
  letter-spacing: normal;
  margin-bottom: 37px;
}

.blog__preview-info a {
  @include underline--center($primary-clr, 100%);
  background-size: 90% 1px;
  color: $second-clr;
  margin-bottom: $gap--sm;
  padding: 6px;

  &:active {
    opacity: 0.3;
  }
}

.blog__preview-img img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}


/* QUERRIES
===================================================================== */
@media screen and (max-width: $screen--md) {
  .blog {
    padding-top: 35px;
    padding-bottom: 30px;
  }

  .blog__head {
    margin-bottom: 63px;
  }

  .blog h2 {
    margin-bottom: 22px;
  }

  .blog__previews {
    grid-template-columns: 1fr;
    gap: 50px;
  }

  .blog__preview {
    grid-column: auto;
    grid-row: auto;

    &--lg .blog__preview-info {
      grid-column: auto;
      grid-row: auto;
      padding-top: 82px;
    }

    &--lg .blog__preview-img {
      grid-column: auto;
      grid-row: auto;
    }
  }
}

@media screen and (max-width: $screen--sm) {
  .blog {
    padding-top: 25px;
  }

  .blog__head {
    margin-bottom: 56px;
    padding-left: $gap--sm;
    padding-right: $gap--sm;
  }

  .blog__previews {
    overflow-x: hidden;
    gap: unset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -$gap--sm;
    margin-right: -$gap--sm;
  }

  .blog__preview {
    grid-template-columns: 1fr;
    min-width: 290px;
    max-width: 340px;
    flex-grow: 1;
    margin: 0 $gap--sm 40px;

    &:last-child {
      margin-bottom: 0;
    }

    &--lg {
      grid-template-rows: auto;
    }

    &--lg .blog__preview-info {
      padding-top: 59px;
      padding-bottom: 69px;
    }

    &--lg img {
      height: auto;
    }
  }

  .blog__preview-info {
    min-height: 256px;
    padding-top: 44px;
    padding-bottom: 53px;
  }

  .blog__preview-img {
    order: -1;
  }
}
