// VARS
// =====================================================================
// COLORS
// ------------------------------------
// Basic colors
$blue: #007bff;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$red: #dc3545;
$orange: #fd7e14;
$yellow: #ffc107;
$green: #28a745;
$teal: #20c997;
$cyan: #17a2b8;
$white: #ffffff;
$black: #000000;
$black--7: rgba($black, 0.7);
$black--5: rgba($black, 0.5);
$grey: #f0eae4;
$grey--7: rgba($grey, 0.7);
$grey--5: rgba($grey, 0.5);
$light:#e5e5e5;

// Colors for content
$primary-clr: #a87b62;
$second-clr: #1d1613;
$accent-clr: $orange;

// Colors for iteraction
$valid-clr: $green;
$error-clr: $red;
$disabled-clr: $grey;

// LAYOUT
// ------------------------------------
$screen--xs: 320px;
$screen--sm: 767px;
$screen--md: 1023px;
$screen--lg: 1240px;
$screen--xl: 1366px;
$screen--xxl: 1440px;
$screen--fhd: 1920px;

$min-width: $screen--xs;
$max-width: $screen--xl;

$gap--sm: 15px;
$gap--md: 45px;
$gap--lg: 98px;

$border-clr: $primary-clr;

// TEXT
// ------------------------------------
$font: "Poppins", system-ui, -apple-system, "Noto Sans", sans-serif;
$font--theme: "Cormorant Garamond", system-ui, -apple-system, "Georgia", serif;
